@import './reset';
@import './variables/global';
@import './themes/normal';
@import './themes/dark';

.app {
  font: var(--font-m);
  color: var(--primary-color);
  background: var(--bg-color);
  min-height: 100vh;
}

*::-webkit-scrollbar {
  width: 12px; /* ширина scrollbar */
  height: 8px;
}

*::-webkit-scrollbar-track {
  background: var(--light-bg-redesigned); /* цвет дорожки */
}

*::-webkit-scrollbar-thumb {
  background-color: var(--icon-redesigned); /* цвет плашки */
  border-radius: 20px; /* закругления плашки */
  border: 1px solid var(--accent-redesigned); /* padding вокруг плашки */
}

.app *::-webkit-scrollbar {
  width: 12px; /* ширина scrollbar */
  height: 8px;
}

.app *::-webkit-scrollbar-track {
  background: var(--card-bg); /* цвет дорожки */
}

.app *::-webkit-scrollbar-thumb {
  background-color: var(--inverted-primary-color); /* цвет плашки */
  border-radius: 20px; /* закругления плашки */
  border: 2px solid var(--primary-color); /* padding вокруг плашки */
}
